import { LOADING, ADD_PRODUCT } from '../actions';

const initialState = {
    products:{
        listProducts:[]
    },
    loading: 'cargando'
};

export const product = (state = initialState, action ) =>{
    switch(action.type){
        case ADD_PRODUCT:
            return {...state,
                products: { ...state.products.listProducts,
                    listProducts: action.payload.products
                },
                loading: null
            };
        case LOADING:
            return {
                ...state,
                loading: 'cargando'
            }
        default: 
            return state;
    }
}