import React, {Component} from 'react';
import { connect } from 'react-redux';
import { PropTypes} from 'prop-types';

import { fetchProducts } from '../../actions/index';

import Card from './card';

import styles from '../../css/home.css';

class MainContainer extends Component{

    componentWillMount(){
        this.props.getProducts(null);
    }

    render(){
        return(
            
            this.props.loading === 'cargando' ? <h1> CARGANDO </h1> :
            <section className="ae-container-fluid rk-main">
                <section className={`ae-container-fluid ae-container-fluid--inner rk-portfolio ${styles.marginTop15}`}>
                    <div  className="ae-masonry ae-masonry-md-2 ae-masonry-xl-4">
                    {
                    this.props.myProducts.map((image, index)=>{
                    return(
                        <Card key={index} image={image}></Card>
                        );
                    })
                    }
                    </div>
                </section>
            </section>
        );
    }
}

MainContainer.propTypes = {
    myProducts: PropTypes.array,
    getProducts: PropTypes.func
}

const mapStateToProps = (state) => ({
    myProducts: state.products.listProducts,
    loading: state.products.loading
});

const mapDispatchToProps = (dispatch) => ({
    getProducts : value => dispatch(fetchProducts(value))
});

export default connect(mapStateToProps, mapDispatchToProps)(MainContainer);